.cardcontents{
  grid-row: 1;
  grid-column: 1;
  width:100%;
  height: 95%;
  place-items: center;
  place-self: center;
  border-left: double 5px var(--yellow);
  border-radius: 4vmin;
  padding-top: 1vh;
  display:grid;
  grid-auto-rows: auto;
  grid-auto-columns: 100%;
}

.h2{
  width:70%;
  background-color: var(--yellow);
  padding: 1vh;
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  border:2px var(--blue) solid;
  border-radius: 2vmin;
}

.attendenceWrapper{
  grid-row: 1;
  grid-column: 2;
  display:grid;
  grid-auto-rows: 14% 86%;
}

.f{
  grid-row: 2;
  grid-column: 1;
  display:grid;
  height: 90%;
  grid-auto-columns: 50% 50% ;
  grid-auto-rows: 100%;
  place-items: center;
  background-color: var(--white);
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
}
 .f1, .f2
{
  grid-row: 1;
  grid-column: auto;
  width:80%;
  height: 90%;
  display: grid;
  grid-auto-rows: 30% 30% 30%;
  grid-auto-columns: 100%;
  row-gap: 3%;
  place-items: center;
}
.f1btn, .f2btn{
  grid-row: auto;
  grid-column: 1;
  width:80%;
  height: 100%;
  font-size: 0.6rem ;
  font-weight: bold;
}

.controlwrapper{
  grid-row: 1;
  grid-column: 3;
  width:95%;
  height: 95%;
  place-self: center;
  border-right: double 5px var(--yellow);
  display:grid;
  grid-auto-rows: 33% 33% 33%;
  border-radius: 4vmin;

}

.f1btn,.f2btn,.cwbtn{
  background-color: var(--blue);
  color: var(--white);
  border-radius: 10px;
  margin-top: 3%;
  place-self: center;
}

.f1btn:hover,.f2btn:hover,.cwbtn:hover{
  background-color: var(--white);
  color: var(--blue);
}

.cwbtn{
  grid-row: auto;
  grid-column: 1;
  width:80%;
  height: 60%;
  font-size: 1rem ;
  font-weight: bold;
}

.errorCard{
  width:75%;
  height:150px;
  display: grid;
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  color: var(--color2);
  border-radius: 4vmin;
  background-color:var(--color4);
  color: var(--color2);
  font-size: 0.8rem ;
  place-content: center;
  place-items: center;
  place-self: center;
}

.f1btn:hover,.f2btn:hover,.cwbtn:hover{
  background-color: var(--white);
  color: var(--blue);
}