.container{
  grid-row: 1;
  grid-column: auto;
  display:grid;
  height: 96%;
  width: 85%;
  grid-auto-columns: 50% 50% ;
  grid-auto-rows: 100%;
  place-items: center;
  background-color: var(--white);
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  margin-left: 15%;
}

 .f1,
 .f2{
    grid-row: 1;
    grid-column: auto;
    width:80%;
    height: 90%;
    display: grid;
    grid-auto-rows: 30% 30% 30%;
    grid-auto-columns: 100%;
    row-gap: 1%;
    place-items: center;
    place-content: center;
    box-shadow: 0px 0px 3px 0px var(--shadowColor);
    background-color: var(--blue);

  }
  .c{
      display:grid;
      grid-row: auto;
      grid-auto-rows: 100%;
      grid-auto-columns: 47% 47%;
      width:100%;
      height:100%;
      column-gap:2% ;
    }

  .label{
    grid-column: 1;
    width:100%;
    color:var(--white)
  }
  .value{
    grid-column: 2;
    width:100%;
    box-shadow: 0px 0px 3px 0px var(--shadowColor);
    background-color: var(--white);
    border-radius: 50%;
  }