.box {
    grid-row: 1;
    grid-column: 1;
    width: 100%;
    height: 60%;
    display: grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 25% 70% auto;
    place-items: center;
    gap: 1%;
}

.boxbtn {
    grid-row: 1;
    grid-column: 1;
    width: 80%;
    height: 60%;
    place-self: center;
    background-color: var(--blue);
    color: var(--white);
    font-size: 1rem;
    font-weight: bold;
    transition: 0.4s;
}
.boxbtn:hover {
    background-color: var(--white);
    color: var(--blue);
    box-shadow: var(--shadowColor);
}

.input {
    grid-row: 1;
    grid-column: 2;
    width: 100%;
    height: 60%;
    box-shadow: inset 1px 1px 5px 2px var(--shadowColor), inset -1px -1px 5px 2px var(--shadowColor);
    color: var(--color2);
    border-radius: 4vmin;
    background-color: rgba(0, 40, 255, 0.3);
    border: 5px var(--color3) double;
    padding: 5px;
    padding-right: 10px;
    place-self: center;
}

.label {
    grid-row: 1;
    grid-column: 1;
    width: 100%;
    height: 100%;
    color: var(--blue);
    place-self: center;
    font-weight: bold;
    padding: 0;
    color: white;
    display: block;
    white-space: nowrap;
}