.conflogoWrapper{
    grid-row: 1;
    grid-column: 1;
    width:100%;
    height: 100%;
    padding:8px;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center;
}
  
.searchWrapper{
    grid-row: 1;
    grid-column: 2;
    width:100%;
    height: 100%;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center; 
}

  .orglogoWrapper{
    grid-row: 1;
    grid-column: 3;
    width:100%;
    height: 100%;
    place-self: center;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center;
}

.countersWrapper{
    grid-row: 1;
    grid-column: 4;
    width:100%;
    height: 100%;
    border-radius: 4vmin;
    border: 5px var(--color3) double;
    padding:8px;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center; 
}

.conflogo {
    grid-row:1;
    grid-column: 1;
    height:100%;
    justify-self: right;
  }

.wpulogo {
    grid-row:1;
    grid-column: 1;
    height:100%;
    justify-self: center;
  }