header{
  background-image: var(--gradient3);
}
main{
  background-image: var(--gradient4);
}
footer{
  background-image: var(--gradient6);
}

.app {
  text-align: center;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  height: 100vh;
  display:grid;
  grid-auto-rows: 17vh 75vh 8vh;
  grid-auto-columns: 100%;
}
.header {
  grid-row: 1;
  grid-column: 1;
  height: 100%;
  width: 100%;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 35% 35% 10% 20%;
  background-color: var(--white);
  box-shadow: 1px 0px 5px 2px var(--shadowColor);
  grid-column-gap: 1%;
  direction: rtl;
}

.contents {
  grid-row: 2;
  grid-column: 1;
  width: 100%;
  height: 100%;
  max-height: 75vh;
  margin: 0px;
  padding-top: 0px;
  display:grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 65% 35%;
  place-items:center;
}

.footer {
  grid-row: 3;
  grid-column: 1;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 18% 18% 18% 18% 18%;
  gap:2%;
  width: 100%;
  height: 8vh;
  place-items:center;
  place-self: center;
  margin: 0px;
  padding: 0px;
  color: var(--color1);
  background-color: var(--blue);
  box-shadow: 1px 0px 5px 2px var(--shadowColor);
}

.cardscontainer{
  grid-row: 1;
  grid-column: 1;
  width: 90%;
  height: 100%;
  max-height: 75vh;
  margin: 0px;
  padding-top: 15px;
  display:grid;
  grid-auto-rows: auto;
  grid-auto-columns: 100%;
  place-items:center;
  overflow: hidden;
  overflow-y: scroll;
  row-gap: 2.5%;
}

.formcontainer{
  grid-row: 1;
  grid-column: 2;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 100%;
  width: 99%;
  height: 100%;
  max-height: 75vh;
  margin: 0px;
  padding:0px;
  overflow: hidden;
}

.card{
  width:75%;
  min-height:200px;
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  border-radius: 4vmin;
  background-color:var(--white);
  color: var(--color2);
  font-size: 0.8rem ;
  display:grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 50% 25% 25%;
  direction: rtl;
}
