.formcontainer {
  grid-row: 1;
  grid-column: 1;
  display: grid;
  grid-auto-rows: 10% 90%;
  grid-auto-columns: 100%;
  width:100%;
  height:100%;
  align-self: center;
  margin-top: 1vh;
}

.title{
  font-size: 1.6vw;
  font-weight: 400;
  border-top:2px gray solid;
  margin:0;
  background-color: var(--yellow);
  grid-row: 1;
  grid-column: 1;
  width: 90%;
  height: 100% ;
  border-radius: 10vh 10vh 0 0;
}

.formcards{
  grid-row: 2;
  grid-column: 1;
  width: 90%;
  height: 97% ;
  background-color:var(--white);
  box-shadow: 1px 4px 8px 0 rgba(0,0,0,0.3);
  cursor: pointer;
  display: grid;
  grid-auto-rows: auto;
  grid-auto-columns: 100%;
  place-items: center;
  overflow: hidden;
  overflow-y: auto;
  border-radius: 0 0 10vh 10vh;

}

.formcard {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 90%;
    height:auto;
    border-radius: 2vh;
    margin-top: 4px;
    padding: 7px;
    background-color: var(--blue);
  }
  
  .formcard:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.6);
    transition: 0.3s;
  }
  
  .label {
    display: block;
    font-weight: 600;
    font-size: 1vim;
    letter-spacing: .1px;
    line-height: 15px;
    color: var(--white);
    width: 95%;
    word-break: break-word;
    text-align: right;
    margin-right: 1vw;
  }

  .star{
      color:crimson;
  }

.formsubmit{
  word-wrap: break-word;
  overflow-wrap: break-word;
  cursor: pointer;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.25px;
  line-height: 36px;
  height: auto;
  text-transform: none;
  text-align: center;
  color: black;
  background-color: var(--yellow);
  box-align: center;
  align-items: center;
  display: flex;
  position: relative;
  padding:0 24px;
  margin-top:10px;
  border-radius: 2vh;
}

.forminputs{
  border-radius: 8px;
  height:2.5rem;
  padding-right: 10px;
  background-color: var(--white);
  box-shadow: 1px 0px 5px 3px var(--shadowColor) inset, 
 -1px 0px 5px 3px var(--shadowColor) inset;
  border:var(--blue) solid 3px;
  text-decoration: none;
  margin: 0;
  word-wrap: break-word;
  overflow-wrap: break-word;
  color:rgba(0,0,0,0.4);
  font-size: 17px;
  font-weight: 500;
  letter-spacing: 0.25px;
  line-height: 32px;
  text-transform: none;
  width: 100%;
  margin-top: 1.5vh;
}
.email{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.formerrorbar{
  margin-top:5px;
  color: crimson;
  word-wrap: break-word;
  overflow-wrap: break-word;
  font-size: 17px;
  font-weight: 600;
  letter-spacing: 0.25px;
  line-height: 36px;
  height: auto;
  text-transform: none;
  border-top: 0px;
  border-right:0px;
  border-left:0px;
  min-width: 350px;
}