/* Webfont: Tajawal-Regular */
@font-face {
    font-family: 'Tajawal';
    src: url('./Web-TT/Tajawal.eot'); /* IE9 Compat Modes */
    src: url('./Web-TT/Tajawal.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./Web-TT/Tajawal.woff') format('woff'), /* Modern Browsers */
         url('./Web-TT/Tajawal.ttf') format('truetype'); /* Safari, Android, iOS */
    font-style: normal;
    font-weight: normal;
    /* text-rendering: optimizeLegibility; */
  }
  /* Webfont: Cairo-Regular */@font-face {
    font-family: 'Cairo';
    src: url('./cairo/Cairo-Regular.eot'); /* IE9 Compat Modes */
    src: url('./cairo/Cairo-Regular.eot') format('embedded-opentype'), /* IE6-IE8 */
         url('./cairo/Cairo-Regular.woff') format('woff'), /* Modern Browsers */
         url('./cairo/Cairo-Regular.ttf') format('truetype'); /* Safari, Android, iOS */
             font-style: normal;
    font-weight: normal;
  }
  
  * {
    /* Setting up Project color theme */
    --yellow: #fecb30;
    --blue:   rgb(0, 90, 170);
    --white:  rgb(255, 240, 240);
    --black:  rgb(0, 0, 0);
    --green:  rgb(20, 150, 20);
    --border1:rgb(0, 90, 170) 2px double;
    --border2:white 2px  double;
    --color1: hsl(350, 100%, 90%);
    --color2: hsl(0, 99%, 6%);
    --yellow2:rgb(255, 255, 100);
    --color4: rgb(255, 141, 100);
    --shadowColor:     hsla(214, 70%, 4%, 0.4);
    --barShadowcolor3: rgb(200, 178, 112, 0.4);
    --gradient1: linear-gradient(to bottom, rgb(53 79 193),   rgb(36 57 145));
    --gradient2: linear-gradient(to bottom, rgb(60 90 200),   rgb(45 70 155));
    --gradient3: linear-gradient(to bottom, rgb(200 200 255), rgb(255 255 200));
    --gradient4: linear-gradient(to bottom, rgb(200 200 170), rgb(255 255 130));
    --gradient5: linear-gradient(to bottom, rgb(50, 200, 75), rgb(36, 145, 57));
    --gradient6: linear-gradient(to bottom, rgb(255 255 200), rgb(200 200 255));
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'cairo';
    /* font-family: Roboto,Arial,sans-serif; */
    direction: rtl;
  
  }
  
  body {
    margin: 0;
    padding: 0;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  